import { Component, OnInit } from '@angular/core';
import {SermonService} from "../../services/sermon.service";
import {Storage} from "@ionic/storage";
import {ActivatedRoute, Router} from "@angular/router";
import {LoaderService} from "../../services/loader.service";
import {ApiService} from "../../services/api.service";
import {IdService} from "../../services/id.service";
import {EventsService} from "../../services/events.service";
import {ToastService} from "../../services/toast.service";
import {Location, formatDate} from "@angular/common";
import {CommonService} from "../../services/common.service";
import {EventProviderService} from "../../services/event-provider.service";
import {AnalyticsService} from "../../services/analytics.service";
import {Platform} from "@ionic/angular";
import {Device} from "@ionic-native/device/ngx";
import {TranslateService} from "@ngx-translate/core";
import {DomSanitizer} from "@angular/platform-browser";
import { SubcribeNewsComponent } from './../common/subcribe-news/subcribe-news.component';
import {ModalController} from "@ionic/angular";
@Component({
  selector: 'app-newsletter-detail',
  templateUrl: './newsletter-detail.page.html',
  styleUrls: ['./newsletter-detail.page.scss'],
})
export class NewsletterDetailPage implements OnInit {

  ch_id: any = '';
  post_id: any = '';
  post: any = '';

  allNotes: any;
  post_url: any;
  banner_image: any;
  banner_desc: any;
  banner_title: any;
  enable_home: any;
  page_id: any;
  post_category_url: any;
  is_external_url: boolean = false;
  start_time_analytics: any;
  page_name: any;
  custom_name: any;
  user: any;
  theme: any;
  alertMessage: any;
  page_in_home_tabs: any = false;
  deviceType: string = '';
  width: number;
  show_index: any = '';
  topics_clicked: boolean = false;
  constructor(
      private storage: Storage,
      private route: ActivatedRoute,
      private loader: LoaderService,
      private api: ApiService,
      private idService: IdService,
      private router: Router,
      private eventsService: EventsService,
      private apiService: ApiService,
      private toast: ToastService,
      private location: Location,
      private common: CommonService,
      private event_provider: EventProviderService,
      private analytics: AnalyticsService,
      private platform: Platform,
      private device: Device,
      private translate: TranslateService,
      private dom: DomSanitizer,
      public modalController: ModalController,
  ) {
    // get translated alert message
    let interval = setInterval(() => {
      this.translate.get('ALERTS').subscribe( (res: any) => {
        this.alertMessage = res;
        clearInterval(interval);
      });
    }, 2000);

    // chekcing if page is opened in home tabs or simple
    let current_url = new URL(window.location.href);
    let page_href = current_url.pathname;
    if (page_href.includes('home-tabs')){
      this.page_in_home_tabs = true;
    } else {
      this.page_in_home_tabs = false;
    }
  }

  ngOnInit() {
    this.ch_id = this.route.snapshot.paramMap.get('ch_id');
    this.post_id = this.route.snapshot.paramMap.get('post_id');

    if (this.ch_id){
      // saving church id in ID Service
      this.idService.setId(this.ch_id);
    }
    if (this.ch_id && this.post_id){
      this.getNewsLetterbyid();
    }
  }

  getNewsLetterbyid(){
    this.api.getnewsletterbyid(this.ch_id, this.post_id).subscribe( (res: any) => {
      if (res && res.status){
        this.post = res.posts;
        if(this.post.subject !== '' && this.post.subject !== null ){
          this.post['subjects_array'] =JSON.parse(this.post.subject) ;
          // ZB changes
      }
      if(this.post.attachment !== "" && this.post.attachment !== null && this.post.attachment.includes("s3.wasabisys.com")  && formatDate(this.post.created_at,'yyyy-MM-dd','en_US') >= '2023-05-29'){
        let abc = this.post.attachment.split("/");
        //  let img_name = abc[abc.length-1];
         let folder_name2 = abc[abc.length-2];
        if(this.platform.is("mobile")){
            this.deviceType = 'radiobase_mobile';
        }else if(this.platform.is("tablet")){
            this.deviceType = 'radiobase_tablet';
        }else{
            this.deviceType = folder_name2;
        }

        let new_link =  this.post.attachment.replace(folder_name2, this.deviceType);
        this.post.attachment = new_link; 
        this.post['folder_name'] = folder_name2;
      }
  

        // if (this.post.attachment){
        //   this.post.attachment = this.dom.bypassSecurityTrustResourceUrl(this.post.attachment);
        // }
      }
    })

  }

  gotobackPage() {
    this.location.back();
  }

// ZB changes

subjectClicked(event,index){
  event.stopPropagation();

  this.topics_clicked = true;
  this.show_index = index;

}
wantSubscription(){
  this.subcribeNews();
}

async subcribeNews() {
  const modal = await this.modalController.create({
    component: SubcribeNewsComponent,
    cssClass: 'CommentModal',
    componentProps: {
    }
  });
  modal.onDidDismiss().then(res => {

  });
  return await modal.present();
  
}



}
